export default ".full-page {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    overflow: hidden;\n}\n.header {\n    background-color: #333;\n    height: 100px;\n    position: relative;\n    display: flex;\n    flex-direction: row;\n    padding: 10px;\n    box-shadow: 5px 5px 1px 1px #888888;\n}\n\n.header-left {\n    position: relative;\n    flex: 1;\n    font-size: 30px;\n    font-style: italic;\n    color: aliceblue;\n    background: #333;\n    text-shadow: 2px 2px 4px rgba(255, 255, 255, 0.5);\n}\n.header-middle {\n    font-size: 30px;\n    margin: 2px 50px 10px 10px;\n    padding: 0 10px;\n    color: aliceblue;\n    line-height: 30px;\n    overflow: hidden;\n    height: 35px;\n    background-color: black;\n}\n.header-right {\n    padding-right: 10px;\n}\n.content-body {\n    position: absolute;\n    top: 60px;\n    bottom: 0;\n    left: 0px;\n    background: rgb(2, 0, 36);\n    background: linear-gradient(\n        90deg,\n        rgba(2, 0, 36, 1) 0%,\n        rgba(9, 9, 121, 1) 7%,\n        rgba(0, 212, 255, 1) 26%\n    );\n    right: 0px;\n    overflow: auto;\n    display: flex;\n}\n#game-board {\n    position: realtive;\n    margin: auto;\n    background-image: url(\"assets/tennis.png\");\n    background-size: cover;\n    display: flex;\n    flex-direction: column;\n    border-radius: 10px;\n    overflow: hidden;\n}\n\n#timer {\n    color: yellow;\n}\n\n.instructions {\n    position: absolute;\n    padding: 20px;\n    width: 500px;\n    transform: translateX(-50%);\n    left: 50%;\n    top: 15px;\n    display: inline-block;\n    background: white;\n    border-radius: 10px;\n    box-shadow: 2px 2px 1px 1px #888888;\n}\n";